// custome style
/*===========================
    Custome style 
 ============================*/
html{
    scroll-behavior: smooth;
}
section { padding: 100px 0; }
section.small-section { padding: 80px 0; }
section.big-section { padding: 140px 0; }

// background colors
a, a:hover { text-decoration: none; }
img { max-width: 100%; }

.main-block { position: relative; background-size: cover; background-position: top left; }
.bg-white {
    background-color: $white;
}
.bg-black {
    background-color: $black;
}
.bg-green {
    background-color: $green;
}
.bg-medium-gray {
    background-color: $medium-gray;
}
.bg-light-gray {
    background-color: $light-gray;
}
.bg-matisse {
    background-color: $matisse;
}
.bg-matisse-dark {
    background-color: $matisse-dark;
}

.overlayer { position: absolute; top: 0; left: 0; height: 100%; width: 100%; opacity: 0.9; }

.bg-cover {
    background-size: cover;
}

.h-100vh {
    height: 100vh;
}

.overlay-block {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    opacity: 0.3;
    height: 100%;
    width: 100%;
}

