/* buttons */
.btn-sm {
    padding: 0.3em 2em;
}
.btn {
    position: relative;
    overflow: hidden;
}
.btn:after {
    position: absolute;
    top: 0;
    content: "";
    left: -100%;
    height: 100%;
    width: 100%;
    z-index: -1;
    @extend %common-transition;
}
.btn:hover:after {
    left: 0;
    @extend %common-transition;
}

.btn-white {
    background-color: $white;
    color: $matisse-dark;
    border-color: $white;
    &:hover {
        color: $white;
    }
}

.btn-green {
    background-color: $green;
    border: 1px solid $green;
    color: $white;
    &:hover {
        color: $green;
    }
}

.btn-white:hover, .btn-black:hover, .btn-green:hover { background-color: transparent; @extend %common-transition; }


.btn-outline-white {
    border-color: $white;
    color: $white;
    &:hover {
        color: $black;
    }
}
.btn-outline-white::after {
    background-color: $white;
}

.btn-outline-green {
    border-color: $green;
    color: $green;
    &:hover {
        color: $white;
    }
}
.btn-outline-green::after {
    background-color: $green;
}

// menu toggle button
.menu-toggle {
    width: 22px;
    display: inline-block;
    position: relative;
    .toggle-line {
        display: block;
        background-color: #fff;
        height: 2px;
        width: 100%;
        margin-bottom: 4px;
        &:last-child {
            margin-bottom: 0;
        }
    }
}

// separators
.separator {
    height: 2px;
    width: 100px;
    display: inline-block;
    vertical-align: middle;
    margin-right: 15px;
}

// Social icons
.social-icons { 
    list-style: none;
    margin-top: 80px;
    padding: 0;
    &.icon-small { 
        margin-top: 0;
        li { font-size: 14px; margin: 0 8px; }
    }
    li {
        display: inline-block;
        font-size: 18px;
        margin: 0 12px;
        &:first-child { margin-left: 0; }
        &:last-child { margin-right: 0; }    
        a{
            color: $white;
            @extend %common-transition;
        }
    }
    a:hover { color: $green; @extend %common-transition; }
    &.outline-circle {
        li a {
            border: 1px solid $white;
            height: 40px;
            width: 40px;
            display: inline-block;
            line-height: 40px;
            border-radius: 100%;
            &:hover { border-color: $green; background-color: $white; color: $green; }
        }
    }
}