.stack-box{
    position: relative;
    @include translateX(-10%);
    @include screen(medium){ 
        @include translateX(0); 
    }
}
.image-overlay-box + section {
    padding-top: 250px;
}
.image-overlay-box{
    padding: 100px 0 0;
    .image-box-wrapper{
        position: relative;
        display: inline-block;
        .image-box-caption {
            position: absolute;
            top: 50%;
            left: 50%;
            @include translateX(-50%);
            @include translateY(-50%);
        }
        .separator {
            position: absolute;
            bottom: 100px;
            right: -270px;
            width: 370px
        }
    }
    &.image-right{
        text-align: right;
        .separator {
            bottom: 100px;
            right: auto;
            left: -270px;
        }
    }
}
.card.card-style-1 {
    border: 0;
    -webkit-border-radius: 70px 70px 70px 0;
    -moz-border-radius: 70px 70px 70px 0;
    border-radius: 70px 70px 70px 0;
    padding: 10%;
    @extend %common-transition;
    .card-title {
        font-size: 30px;
        font-weight: 600;
        margin-bottom: 60px;
    }
    .card-icon {
        position: absolute;
        bottom: -70px;
        left: 30px;
        opacity: 0;
        @extend %common-transition;
    }
    &:hover{
        background-color: $green;
        color: $white;
        @extend %common-transition;
        .card-icon{
            bottom: -45px;
            opacity: 1;
            @extend %common-transition;
        }
    }
}
.feature-box { 
    .feature-image {
        img{ 
            border-radius: 100%;
            margin-bottom: 30px;
        }
        position: relative;
        .feature-label { 
            position: absolute;
            top: 0;
            right: 16%;
            background-image: url('../images/feature-box-label.png');
            background-size: cover;
            height: 64px;
            width: 66px;
            line-height: 64px;
            text-align: center;
            color: $white;
            font-size: 22px;
            @include screen(large){
                right: 10%;
            }
            @include screen(medium){
                right: 5%;
                top: 10px;
                font-size: 18px;
                height: 44px;
                width: 46px;
                line-height: 44px;
            }
            @include screen(small){
                right: auto;
                top: -20px;
                left: 50%;
                transform: translateX(-50%);
            }
        }
    }
    .feature-title { 
        margin-bottom: 15px; 
    }
    .feature-content {  }
}
.side-image-over-flow { 
    position: absolute; 
    top: -60px; 
    @include screen(small){
        position: unset;
        margin-top: 30px;
    }
}