
// text size
.text-extra-small { font-size: 10px; }
.text-small { font-size: 12px; }
.text-normal { font-size: 14px; }
.text-medium { font-size: 16px; }
.text-extra-medium { font-size: 18px; }
.text-large { font-size: 20px; }

.title-small { font-size: 24px; }
.title-medium { font-size: 28px; }
.title-large { font-size: 36px; }
.title-extra-large { font-size: 72px; }

// text colors
.text-black {
    color: $black;
}
.text-green {
    color: $green;
}
.text-extra-light-gray {
    color: $extra-light-gray;
}
.text-medium-gray {
    color: $medium-gray;
}
.text-light-gray {
    color: $light-gray;
}
.text-matisse {
    color: $matisse;
}

// font weight
.fw-light { font-weight: $font-weight-light; }
.fw-normal { font-weight: $font-weight-normal; }
.fw-medium { font-weight: $font-weight-medium; }
.fw-semi-bold { font-weight: $font-weight-semi-bold; }
.fw-bold { font-weight: $font-weight-bold; }

.line-height-one { line-height: 1; }
.line-height-two { line-height: 2; }
.line-height-three { line-height: 3; }
.line-height-normal { line-height: normal; }