.tab-style-1 {  }
.tab-style-2.nav-pills { 
    a.nav-link {
        white-space: nowrap;
        color: $black;
        border-bottom: 1px solid transparent;
        padding: 0;
        text-align: left;
        &.active { 
            background-color: transparent;
            border-bottom-color: $green;
            color: $green;
        }
    }
    @include screen(medium){
        li.mr-45px { margin-right: 0 !important; width: 100%; padding: 5px 0; }
    }
}
.tab-style-2 { 
    .tab-content {
        border-left: 8px solid $green;
    }
}

.tab-style-3 { 
    padding: 60px 0;
    z-index:1;
    @include screen(small){ padding: 30px 10px }
    a.nav-link {
        font-size: 24px;
        display: block;
        margin-bottom: 30px;
        position: relative;
        color: $white;
        padding: 10px 30px;
        z-index: 1;
        white-space: nowrap;
        @include screen(medium){
            font-size: 16px;
            margin-bottom: 10px;
            padding: 10px 15px;
        }
        &:after {
            position: absolute;
            top: 0;
            left: 0;
            height:100%;
            width: 100%;
            content: "";
            background-color: $white;
            z-index: -1;
            opacity: 0;
            box-shadow: 0 0 5px #ddd;
            @extend %common-transition;
            @include translateX(-5%);
            @include screen(small){ 
                box-shadow: none;
                border-radius: 30px; 
                @include translateX(0); 
            }
        }
        &.active, &:hover, &.active:hover { 
            color: $matisse-dark;
            @extend %common-transition;
            &:after{
                opacity: 1;
                @extend %common-transition;
                @include translateX(-10%);
                @include screen(small){ 
                    @include translateX(0); 
                }
            }
        }
    }
}