.side-image-over-flow { position: absolute; top: -60px; }

@media only screen and (max-width: 767px) { .side-image-over-flow { position: unset; margin-top: 30px; } }

.tab-style-2.nav-pills a.nav-link { white-space: nowrap; color: #000; border-bottom: 1px solid transparent; padding: 0; text-align: left; }

.tab-style-2.nav-pills a.nav-link.active { background-color: transparent; border-bottom-color: #4dbb6d; color: #4dbb6d; }

@media only screen and (max-width: 991px) { .tab-style-2.nav-pills li.mr-45px { margin-right: 0 !important; width: 100%; padding: 5px 0; } }

.tab-style-2 .tab-content { border-left: 8px solid #4dbb6d; }

.tab-style-3 { padding: 60px 0; z-index: 1; }

@media only screen and (max-width: 767px) { .tab-style-3 { padding: 30px 10px; } }

.tab-style-3 a.nav-link { font-size: 24px; display: block; margin-bottom: 30px; position: relative; color: #fff; padding: 10px 30px; z-index: 1; white-space: nowrap; }

@media only screen and (max-width: 991px) { .tab-style-3 a.nav-link { font-size: 16px; margin-bottom: 10px; padding: 10px 15px; } }

.tab-style-3 a.nav-link:after { position: absolute; top: 0; left: 0; height: 100%; width: 100%; content: ""; background-color: #fff; z-index: -1; opacity: 0; box-shadow: 0 0 5px #ddd; -webkit-transform: translateX(-5%); -moz-transform: translateX(-5%); -ms-transform: translateX(-5%); -o-transform: translateX(-5%); transform: translateX(-5%); }

@media only screen and (max-width: 767px) { .tab-style-3 a.nav-link:after { box-shadow: none; border-radius: 30px; -webkit-transform: translateX(0); -moz-transform: translateX(0); -ms-transform: translateX(0); -o-transform: translateX(0); transform: translateX(0); } }

.tab-style-3 a.nav-link.active, .tab-style-3 a.nav-link:hover, .tab-style-3 a.nav-link.active:hover { color: #1f2933; }

.tab-style-3 a.nav-link.active:after, .tab-style-3 a.nav-link:hover:after, .tab-style-3 a.nav-link.active:hover:after { opacity: 1; -webkit-transform: translateX(-10%); -moz-transform: translateX(-10%); -ms-transform: translateX(-10%); -o-transform: translateX(-10%); transform: translateX(-10%); }

